<ul class="projects"><li class="projects__item" v-for="(item, index) in projects" :key="item.img"><div class="projects__imgWrapper" v-cloak v-show="loadedImages[index]"><img class="projects__img" :src="item.img" alt="alt" loading="lazy" v-motion-slide-visible-top :initial="{opacity: 0, y: 100,}" :enter="{ opacity: 1, y: 0, scale: 1 }" :variants="{ custom: { scale: 1 } }" :delay="100"></div><div class="loading" v-show="!loadedImages[index]">Loading...</div><div class="projects__content" v-motion-pop-visible :enter="{ opacity: 1, y: 0, scale: 1 }" :variants="{ custom: { scale: 2 } }" :hovered="{ scale: 1 }" :delay="100"><span class="projects__geo">{{item.geo}}</span><h3 class="projects__title">{{item.title}}</h3><span class="projects__url">{{item.more}}</span></div></li></ul>