<div class="storeCard"><div class="storeCard__imgWrapper" v-if="animationDirection == 'left'"><img class="storeCard__img" :src="src" alt="alt" loading="lazy" v-motion-slide-visible-right :enter="{ opacity: 1, y: 0, scale: 1 }" :variants="{ custom: { scale: 2 } }" :hovered="{ scale: 1 }" :delay="100"></div><div class="storeCard__imgWrapper" v-else-if="animationDirection == 'right'"><img class="storeCard__img" :src="src" alt="alt" loading="lazy" v-motion-slide-visible-left :enter="{ opacity: 1, y: 0, scale: 1 }" :variants="{ custom: { scale: 2 } }" :hovered="{ scale: 1 }" :delay="100"></div><div class="storeCard__descriptionBlock"><div class="storeCard__textBlock"><h3 class="storeCard__cardTitle">{{title}}</h3><span class="storeCard__cardText">{{text}}</span></div><img class="storeCard__arrow" :src="arrowStore" loading="lazy" alt="arrow"></div></div>