<div class="recommendedContent"><div class="recommendedContent__header"><p class="recommendedContent__text">{{description}}</p><h2 class="recommendedContent__title">{{title}}</h2></div><ul class="recommendedContent__wrapper"><li class="recommendedContent__item" v-for="project in items" :key="project.id"><div class="recommendedContent__imgWrapper"><img class="recommendedContent__img" :src="project.src" loading="lazy" :alt="project.alt"><div class="recommendedContent__textsBlock"><span class="recommendedContent__name">{{project.name}}</span><h3 class="recommendedContent__itemTitle">{{project.title}}</h3><a class="recommendedContent__more" href="javascript">{{project.more}}</a></div></div></li></ul></div>